import React, { useRef, useState } from 'react';
import {
  GoogleMap,
  Marker,
  useJsApiLoader,
  InfoWindow,
} from '@react-google-maps/api';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { useSelector, useDispatch } from 'react-redux';
import usePlacesAutocomplete, { getGeocode } from 'use-places-autocomplete';
import '@reach/combobox/styles.css';
import { formatRelative } from 'date-fns';
import mapStyle from '../Common/utils/mapStyle';
import '../Common/Css/main.css';
import { geocodeByAddress, getLatLng } from 'react-places-autocomplete';
import { SearchData } from '../Action';
const containerStyle = {
  width: '100vw',
  height: '90vh',
};
const center = {
  lat: 43.653225,
  lng: -79.383186,
};
const libraries = ['places'];

function Drawmap() {
  const { isLoaded } = useJsApiLoader({
    id: 'Maps API Key',
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
    libraries,
  });
  const mapref = React.useRef();
  const [selected, setSelected] = useState(null);
  const [marker, setMarker] = useState([]);
  const [map, setMap] = React.useState(/**@type google.maps.Map*/ (null));
  const onLoad = React.useCallback(function callback(map) {
    mapref.current = map;
  }, []);

  const onUnmount = React.useCallback(function callback(map) {
    setMap(null);
  }, []);

  const onMapClick = React.useCallback((e) => {
    setMarker((current) => [
      ...current,
      {
        lat: e.latLng.lat(),
        lng: e.latLng.lng(),
        time: new Date(),
      },
    ]);
  }, []);
  const panTo = React.useCallback(({ lat, lng }) => {
    mapref.current.panTo({ lat, lng });
    mapref.current.setZoom(10);
  }, []);
  const options = {
    styles: mapStyle,
    disableDefaultUI: true,
    zoomControl: true,
  };
  return isLoaded ? (
    <>
      <div className="well">
        <Search panTo={panTo} />
        <Locate panTo={panTo} />
      </div>

      <GoogleMap
        id="map"
        mapContainerStyle={containerStyle}
        center={center}
        zoom={8}
        onLoad={onLoad}
        onUnmount={onUnmount}
        options={options}
        onClick={onMapClick}
      >
        <>
          <Marker position={center} />

          {marker.map((markers) => (
            <Marker
              key={markers.time.toISOString()}
              position={{ lat: markers.lat, lng: markers.lng }}
              onClick={() => {
                setSelected(markers);
              }}
              icon={{
                url: 'data:image/png;base64,',

                origin: new window.google.maps.Point(0, 0),
                anchor: new window.google.maps.Point(15, 15),
                scaledSize: new window.google.maps.Size(30, 30),
              }}
            />
          ))}
          {selected ? (
            <InfoWindow
              position={{ lat: selected.lat, lng: selected.lng }}
              onCloseClick={() => {
                setSelected(null);
              }}
            >
              <div>
                <h2>Selected Spot</h2>
                <p>Spotted {formatRelative(selected.time, new Date())}</p>
              </div>
            </InfoWindow>
          ) : null}
        </>
      </GoogleMap>
    </>
  ) : (
    <>false</>
  );
}

export default React.memo(Drawmap);

function Locate({ panTo }) {
  return (
    <img
      src="https://flyclipart.com/thumbs/blue-and-white-compass-svg-clip-arts-600-x-516-px-blue-compass-rose-946054.png"
      alt="compass"
      className="locate"
      onClick={() => {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            panTo({
              lat: position.coords.latitude,
              lng: position.coords.longitude,
            });
          },
          () => null
        );
      }}
    />
  );
}

function Search({ panTo }) {
  const {
    ready,
    value,
    suggestions: { status, data },
    setValue,
    clearSuggestions,
  } = usePlacesAutocomplete({
    requestOptions: {
      location: { lat: () => 43.6532, lng: () => -79.3832 },
      radius: 100 * 1000,
    },
  });
  const myState = useSelector((state) => state.SeletedPlace);
  // console.log(myState.target.value);
  const dispatch = useDispatch();
  const handleSelect = async (address) => {
    dispatch(SearchData(address));
    setValue(address, false);
    clearSuggestions();
    try {
      const results = await getGeocode({ address });
      const { lat, lng } = await getLatLng(results[0]);
      panTo({ lat, lng });
    } catch (error) {
      console.log(' Error: ', error);
    }
  };
  return (
    <div className="search">
      <Autocomplete
        autoHighlight
        options={data.map((description) => description.description)}
        onChange={(event, value) => handleSelect(value)} // prints the selected value
        sx={{ width: 300 }}
        renderInput={(params) => (
          <TextField
            {...params}
            label="Search Location"
            variant="outlined"
            fullWidth
            value={value}
            onChange={(event) => {
              const { value } = event.target;
              setValue(value);
            }}
          />
        )}
      />
    </div>
  );
}
